<template>
  <div class="clz-list">
    <template
      v-for="(item,index) in clzes"
      :title="item.ClassID"
    >
      <section
        :key="index"
        class="clz-box"
        @click="jump('ClassroomDetail',{classId:item.ClassID},{clz:item})"
      >
        <div class="clz-box-hd">
          <div class="clz-box-hd-img-box">
            <div class="clz-box-hd-img-box-main">
              <span class="clz-box-stage">{{ `${item.Stage}-${item.CourseInfoName}` }}</span>
              <img
                :src="item.CoverPath"
                class="clz-box-hd-img-box-main-img"
              />
            </div>
          </div>
          <div class="clz-box-hd-content index-main content-font">
            <h3 class="title-font">{{item.Name}}</h3>
            <span>讲师：{{item.TeacherName}}</span>
            <span class="clz-box-hd-content-rank">职称：{{item.TeacherRankCode}}</span>
            <span>开课时间：{{item.BeginDate | formatYMD}}</span>
          </div>
        </div>
        <section class="clz-box-ft index-line oriange-font">
          <h3 class="clz-box-ft-price">{{ `￥${item.LessonPrice /2}` }}/课时</h3>
          <span>{{ `共${item.LessonNumber *2}课时` }}</span>
        </section>
      </section>
    </template>
    <transition
      name="van-slide-up"
      mode="out-in"
    >
      <actionsheet
        v-model="show"
        :actions="actions"
        @select="onSelect"
      />
    </transition>
    <share-lead ref="shareLead" />
  </div>
</template>

<script>
import { Actionsheet } from "vant";
import ShareLead from "@/components/ShareLead";
import share from "@/mixins/share";
export default {
  mixins: [share],
  props: {
    clzes: {
      type: Array,
      required: true
    }
  },
  components: { Actionsheet, ShareLead },
  mounted() {
    this.$bus.$on("share", () => {
      if (this.userInfo) {
        this.refererClass();
      } else {
        this.$dialog
          .confirm({
            title: "需要登录",
            message: "请先登录，再进行下一步操作",
            messageAlign: "left"
          })
          .then(() => {
            this.login()
          })
          .catch(() => {
            // on cancel
          });
      }
    });
  },
  destroyed() {
    this.$bus.$off("share");
  },
  data() {
    return {
      show: false,
      actions: [
        {
          name: "分享给学生",
          role: "s"
        },
        {
          name: "分享给伙伴",
          role: "p"
        }
      ]
    };
  },
  methods: {
    onSelect(item) {
      this.show = false;
      this.$nextTick(() => {
        this.$refs.shareLead.show = true;
      });
      this.setShareData(
        item.role,
        this.clzes.map(clz => clz.ClassID).join(",")
      );
    },
    refererClass() {
      this.$store
        .dispatch("refererClassV2", {
          ClassRefererCodes: this.clzes
            .map(clz => clz.ClassRefererCode)
            .join(","),
          Referer1AccountID: this.$route.query.RefererAccountID || ""
        })
        .then(() => {
          this.show = true;
        })
        .catch(() => {
          this.show = true;
        });
    }
  }
};
</script>

<style lang="less" scoped>
.clz-list {
  background-color: #ffffff;
  .clz-box {
    position: relative;
    border-bottom: 2px solid #eee;
    background-color: #fff;
    color: #666;
    padding: 30px 0;
    line-height: normal;
    &-stage {
      font-size: 20px;
      padding: 2px 5px;
      position: absolute;
      top: 25px;
      color: #ffffff;
      background-color: @orange;
      border: @orange solid 1px;
      border-radius: 0 10px 10px 0;
    }
    &-hd {
      display: flex;
      justify-content: flex-start;
      align-items: stretch;
      padding: 0 20px;
      &-img-box {
        position: relative;
        flex: none;
        z-index: 0;
        &-main {
          position: relative;
          width: 250px;
          height: 180px;
          &-img {
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 18px 0 0 18px;
          }
        }
      }
      &-content {
        padding-left: 30px;
        &-rank {
          color: @blue;
        }
      }
    }
    &-ft {
      padding-top: 30px;
      padding-left: 290px;
      padding-right: 20px;
      &-price {
        font-weight: 600;
        font-size: @title-size;
      }
    }
  }
}
</style>
