<template>
  <div class="clz-list">
    <list
      v-model="loading"
      :finished="finished"
      :error.sync="error"
      @load="getClzes"
    >
      <classes :clzes="clzes" />
    </list>
  </div>
</template>

<script>
import createList from "@/utils/create/create-list";
import savePosition from "@/mixins/save-position";
import Classes from "@/components/Classes";
export default createList({
  name: "Classroom",
  mixins: [savePosition],
  components: { Classes },
  mounted() {
    this.getClzes();
  },
  data() {
    return {
      clzes: []
    };
  },
  methods: {
    getClzes() {
      this.loading = true;
      this.$store
        .dispatch("getTopTeacherClassList", {
          PageIndex: this.PageIndex,
          PageSize: this.PageSize
        })
        .then(({ data, total }) => {
          this.clzes = this.clzes.concat(data);
          this.loading = false;
          this.total = this.clzes.length;
          this.complateLoad(total);
        })
        .catch(() => {
          this.errorLoad();
        });
    }
  }
});
</script>
