import {
  setShareData
} from '@/utils/weixin'
import {
  encodeUrl
} from '@/utils'

export default {
  methods: {
    setShareData(role, classIDs) {
      setShareData({
        desc: this.isStudent(role) ?
          '精选名校名师坐镇' : '创建自己的辅导机构，成就人生梦想',
        link: this.getShareLink(role, classIDs),
        title: this.isStudent(role) ? '课鱼龙门课堂' : '招募课鱼伙伴',
        imgUrl: this.isStudent(role) ? 'https://keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/share_student.jpg' : 'https://keyushijian-1257034971.cos.ap-shanghai.myqcloud.com/ft/image/share_teacher.jpg'
      })
    },
    isStudent(role) {
      return role === 's'
    },
    getShareLink(role, classIDs) {
      if (!role) role = 'p'
      let requestJson = {
        ClassIDs: classIDs,
        RefererAccountID: (this.userInfo && this.userInfo.AccountID) || '',
        role
      }
      return `http://${window.location.host}#explain?${encodeUrl(requestJson)}`
    }
  }
}
