<template>
  <popup
    v-model="show"
    position="top"
    transition="van-fade"
  >
    <div class="lead" @click="show = false">
      <img src="../../assets/lead-arrow.png"/>
    </div>
  </popup>
</template>

<script>
import { Popup } from "vant";
export default {
  components: { Popup },
  data() {
    return {
      show: false
    };
  }
};
</script>

<style lang="less" scoped>
.van-popup {
  background-color: transparent;
}
.lead {
  padding-top: 40px;
  text-align: center;
}
</style>
